// header
.header {
  padding: 4rem 0 0;

  .title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    h1 {
      font-weight: bolder;
    }

    h2 {
      font-weight: bolder;
    }
  }

  .currentLocalWeather {
    margin-top: 2rem;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 4rem;

    background-color: rgba(0,0,0,0.3);
    border-radius: 1rem;

    div {
      h3 {
        font-size: 1.5rem;
        font-weight: bolder;

        span {
          color: #d36100;
        }
      }
    }

    img {
      width: 60px;
      height: 60px;
    }
  }
}

@media only screen and (max-width: 500px) {
  .header {
    .title {
      flex-direction: column;
      align-items: flex-start;
    }

    .currentLocalWeather {
      flex-direction: column;

      padding: 2rem;
      
      img {
        width: 40px;
        height: 40px;
      }
    }
  }
}
