// layout
.App {
  width: 100%;

  background: url(../../image/App/backgroundImage.png) no-repeat center;
  background-size: cover;

  .AppCover {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);

    .Wrapper {
      margin: 0 auto;

      width: 1200px;
    }
  }
}

@media only screen and (max-width: 1300px) {
  .App {
    .AppCover {
      .Wrapper {
        margin: 0 auto;

        width: 90%;
      }
    }
  }
}
