:root{--mainfont-nanum: "nanumSquareNeo";--mainfont-nexon: "nexonLv1";--black: #000;--white: #fff;font-family:var(--mainfont-nanum),var(--mainfont-nexon);font-size:16px;line-height:1.5;font-weight:400;font-synthesis:none;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-text-size-adjust:100%}@media(max-width: 1200px){:root{font-size:16px}:root input{font-size:16px}}@media(max-width: 1000px){:root{font-size:14px}:root input{font-size:14px}}@media(max-width: 800px){:root{font-size:12px}:root input{font-size:12px}}@media(max-width: 600px){:root{font-size:10px}:root input{font-size:10px}}*,*:before,*:after{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}body,button,dd,dl,dt,fieldset,form,h1,h2,h3,h4,h5,h6,input,legend,li,ol,p,select,table,td,textarea,th,ul,figure,figcaption{margin:0;padding:0}body,button,input,select,table,textarea{font-family:var(--mainfont-nanum),var(--mainfont-nexon),"Apple SD Gothic Neo","Malgun Gothic","맑은 고딕",helvetica,sans-serif}a,a:hover,a:focus{color:inherit;text-decoration:none}h1,h2,h3,h4,h5,h6{font-weight:normal}ul{list-style:none}em,address{font-style:normal}strong{font-weight:normal}img{vertical-align:top}input,textarea,button,select{-webkit-appearance:none;-moz-appearance:none;appearance:none;border-radius:0;border:0}body{background-color:var(--black);color:var(--white)}.App{width:100%;background:url(../../image/App/backgroundImage.png) no-repeat center;background-size:cover}.App .AppCover{width:100%;height:100%;background-color:rgba(0,0,0,.5)}.App .AppCover .Wrapper{margin:0 auto;width:1200px}@media only screen and (max-width: 1300px){.App .AppCover .Wrapper{margin:0 auto;width:90%}}.header{padding:4rem 0 0}.header .title{display:flex;flex-direction:row;justify-content:space-between;align-items:center}.header .title h1{font-weight:bolder}.header .title h2{font-weight:bolder}.header .currentLocalWeather{margin-top:2rem;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:4rem;background-color:rgba(0,0,0,.3);border-radius:1rem}.header .currentLocalWeather div h3{font-size:1.5rem;font-weight:bolder}.header .currentLocalWeather div h3 span{color:#d36100}.header .currentLocalWeather img{width:60px;height:60px}@media only screen and (max-width: 500px){.header .title{flex-direction:column;align-items:flex-start}.header .currentLocalWeather{flex-direction:column;padding:2rem}.header .currentLocalWeather img{width:40px;height:40px}}.main{margin-top:3rem}.main .searchBox h2{font-weight:bolder}.main .searchBox .searchbox{position:relative;margin-top:2rem;width:100%}.main .searchBox .searchbox input{padding:0 2rem;width:100%;height:40px;background-color:var(--white);box-shadow:1px 1px 10px var(--black);color:var(--black)}.main .searchBox .searchbox .live_search_box{position:absolute;padding:0 2rem;display:flex;flex-direction:column;gap:1rem;width:100%;max-height:0;background-color:var(--white);color:var(--black);overflow:hidden;transition:all .5s}.main .searchBox .searchbox .live_search_box.on{padding:1rem 2rem;width:100%;max-height:500px;overflow-y:scroll}.main .searchInfo{margin-top:2rem;display:flex;justify-content:space-between;align-items:center;gap:2rem}.main .searchInfo li{display:flex;justify-content:space-between;align-items:center;padding:2rem;width:100%;border-radius:1rem}.main .searchInfo li div h3{font-weight:bolder}.main .searchInfo li div p{font-weight:bolder}.main .searchInfo li img{width:60px;height:60px}.main .searchInfo li:nth-of-type(1){background:linear-gradient(120deg, rgb(255, 233, 149) 0%, rgb(255, 255, 255) 100%);color:#d36100}.main .searchInfo li:nth-of-type(2){background:linear-gradient(120deg, rgb(182, 217, 255) 0%, rgb(255, 255, 255) 100%);color:#004ca1}.main .searchInfo li:nth-of-type(3){background:linear-gradient(120deg, rgb(180, 175, 255) 0%, rgb(255, 255, 255) 100%);color:#0a00bb}.main .searchChart{margin-top:3rem}.main .searchChart h2{font-weight:bolder}.main .searchChart .chart{margin-top:2rem}.main .forecastWeather{margin-top:3rem}.main .forecastWeather h2{font-weight:bolder}.main .forecastWeather ul{margin-top:2rem;display:flex;flex-direction:column;gap:1rem}.main .forecastWeather ul li{display:flex;justify-content:space-between;align-items:center;gap:2rem;padding:1rem;background-color:rgba(0,0,0,.5);border-radius:1rem}.main .forecastWeather ul li div{display:flex;justify-content:space-between;align-items:center}.main .forecastWeather ul li div p{width:80px}.main .forecastWeather ul li div img{width:20px}.main .forecastWeather ul li p{flex:1}@media only screen and (max-width: 900px){.main .searchInfo{flex-direction:column}.main .searchInfo li img{width:40px;height:40px}.main .forecastWeather ul li{flex-direction:column;align-items:flex-start}.main .forecastWeather ul li div{display:flex;justify-content:space-between;align-items:center;width:100%}}.footer{margin-top:4rem;padding:2rem 0;width:100%;border-top:1px solid var(--white);text-align:center}.error{position:relative;width:100%;height:100vh;text-align:center}.error .error_info{display:flex;flex-direction:column;justify-content:center;align-items:center;gap:1rem;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);padding:6rem;background-color:rgba(0,0,0,.5);border-radius:1rem}.error .error_info button{padding:.5rem 1.5rem;border-radius:.5rem;border-top:1px solid var(--white);border-bottom:1px solid var(--white);background-color:rgba(0,0,0,0);color:var(--white);cursor:pointer}.loading{position:fixed;display:flex;justify-content:center;align-items:center;width:100%;height:100vh}