// main
.main {
  margin-top: 3rem;

  .searchBox {
    h2 {
      font-weight: bolder;
    }

    .searchbox {
      position: relative;

      margin-top: 2rem;

      width: 100%;

      input {
        padding: 0 2rem;

        width: 100%;
        height: 40px;
        background-color: var(--white);
        box-shadow: 1px 1px 10px var(--black);
        color: var(--black);
      }

      .live_search_box {
        position: absolute;

        padding: 0 2rem;

        display: flex;
        flex-direction: column;
        gap: 1rem;

        width: 100%;
        max-height: 0;
        background-color: var(--white);
        color: var(--black);

        overflow: hidden;
        transition: all 0.5s;
      }

      .live_search_box.on {
        padding: 1rem 2rem;
        
        width: 100%;
        max-height: 500px;

        overflow-y: scroll;
      }
    }
  }

  .searchInfo {
    margin-top: 2rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;

    li {
      display: flex;
      justify-content: space-between;
      align-items: center;

      padding: 2rem;

      width: 100%;
      border-radius: 1rem;

      div {
        h3 {
          font-weight: bolder;
        }

        p {
          font-weight: bolder;
        }
      }

      img {
        width: 60px;
        height: 60px;
      }
    }

    li:nth-of-type(1) {
      background: linear-gradient(
        120deg,
        rgba(255, 233, 149, 1) 0%,
        rgba(255, 255, 255, 1) 100%
      );

      color: #d36100;
    }

    li:nth-of-type(2) {
      background: linear-gradient(
        120deg,
        rgba(182, 217, 255, 1) 0%,
        rgba(255, 255, 255, 1) 100%
      );

      color: #004ca1;
    }

    li:nth-of-type(3) {
      background: linear-gradient(
        120deg,
        rgba(180, 175, 255, 1) 0%,
        rgba(255, 255, 255, 1) 100%
      );

      color: #0a00bb;
    }
  }

  .searchChart {
    margin-top: 3rem;

    h2 {
      font-weight: bolder;
    }

    .chart {
      margin-top: 2rem;
    }
  }

  .forecastWeather {
    margin-top: 3rem;

    h2 {
      font-weight: bolder;
    }

    ul {
      margin-top: 2rem;

      display: flex;
      flex-direction: column;
      gap: 1rem;

      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;

        padding: 1rem;

        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 1rem;

        div {
          display: flex;
          justify-content: space-between;
          align-items: center;

          p {
            width: 80px;
          }

          img {
            width: 20px;
          }
        }

        p {
          flex: 1;
        }
      }
    }
  }
}

@media only screen and (max-width: 900px) {
  .main {
    .searchInfo {
      flex-direction: column;

      li {
        img {
          width: 40px;
          height: 40px;
        }
      }
    }

    .forecastWeather {
      ul {
        li {
          flex-direction: column;
          align-items: flex-start;

          div {
            display: flex;
            justify-content: space-between;
            align-items: center;

            width: 100%;
          }
        }
      }
    }
  }
}
